
.container {
    min-height: 100vh;
    width: 25vw;
    display: flex;
    justify-content: center;
    align-items: center ;
    flex-direction: column;
}



.container > * {
    margin: 10px;
}

.container > input[type="submit"] {
    width: 100%;
}

.container > h1 {
    font-size: 50px;
    margin-bottom: 30px;
}

@media (max-width: 700px) {
    .container {
        width: 300px;
    }
}